@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
* { 
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}


body  { 
  font-family: 'PT Sans', sans-serif;
}
/* NAVBAR STYLES START*/ 
.navbar {
  background: #1c2237;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 999;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  height: 80px;
}

.container {
  z-index: 1;
  width: 100%;
  max-width: 1300px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 50px;
  padding-left: 50px;
}

.navbar-logo {
  color: #fff;
  justify-self: start;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.navbar-icon {
  margin-right: 0.5rem;
}

.nav-menu {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
  text-align: center;
  justify-content: end;
}

.nav-item {
  height: 80px;
  border-bottom: 2px solid transparent;
}

.nav-item:hover {
  border-bottom: 2px solid #f00946;
}

.nav-links {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
}

.fa-bars {
  color: #fff;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #1c2237;
    left: 0;
    opacity: 1;
    transition: all 0.6s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    color: #f00946;
    transform: scale(1.2);
    transition: all 0.3s ease;
  }

  .nav-item:hover {
    border: none;
  }

  .nav-item {
    width: 100%;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
  }

  .nav-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 120px;
  }
}

.lead { 
  font-size: 18px;
}
/* NAVBAR STYLES END  */


.footer-container {
  background-color: #1c2237;
  padding: 4rem 0 2rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 40vh;
}

.footer-subscription {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: 24px;
  padding: 24px;
  color: #fff;
}

.footer-subscription-heading {
  margin-bottom: 24px;
  font-size: 24px;
}

.footer-subscription-text {
  margin-bottom: 24px;
  font-size: 20px;
}

.footer-input {
  padding: 8px 24px;
  border-radius: 2px;
  margin-right: 10px;
  outline: none;
  border: none;
  font-size: 18px;
  margin-bottom: 16px;
  border: 1px solid #fff;
}

.footer-links {
  width: 100%;
  max-width: 1000px;
  display: flex;
  justify-content: center;
}

.footer-link-wrapper {
  display: flex;
}

.footer-link-items {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 16px;
  text-align: left;
  width: 160px;
  box-sizing: border-box;
}

.footer-link-items h2 {
  margin-bottom: 16px;
}

.footer-link-items > h2 {
  color: #fff;
}

.footer-link-items a {
  color: #fff;
  text-decoration: none;
  margin-bottom: 0.5rem;
}

.footer-link-items a:hover {
  color: #e9e9e9;
  transition: 0.3s ease-out;
}

.footer-email-form h2 {
  margin-bottom: 2rem;
}

.footer-input::-webkit-input-placeholder {
  color: #b1b1b1;
}

.footer-input:-ms-input-placeholder {
  color: #b1b1b1;
}

.footer-input::-ms-input-placeholder {
  color: #b1b1b1;
}

.footer-input::placeholder {
  color: #b1b1b1;
}

/* Social Icons */
.social-icon-link {
  color: #fff;
  font-size: 24px;
  cursor: pointer;
}

.social-icon-link:hover {
  color: #2ab7ca;
  cursor: pointer;
}

.social-icon-link a:hover {
  color: #2ab7ca;
  cursor: pointer;
}

.social-media {
  max-width: 1000px;
  width: 100%;
}

.social-media-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  max-width: 1000px;
  margin: 40px auto 0 auto;
}

.social-icons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 150px;
  cursor: pointer;
}

.social-logo {
  color: #fff;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}

.website-rights {
  color: #fff;
  margin-bottom: 16px;
}

@media(max-width: 820px) {
  .footer-links {
    padding-top: 2rem;
  }

  .footer-input {
    width: 100%;
  }

  .btn {
    width: 100%;
  }

  .footer-link-wrapper {
    flex-direction: column;
  }

  .social-media-wrap {
    flex-direction: column;
  }
}


@media(max-width: 786px) { 
  .social-media { 
    flex-direction: column;
  }

  .social-logo { 
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}
.project-wrapper {
  min-height: 55vh;
  min-width: 75vw;
  background-color: #1c2237;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

img {
  border-top-left-radius: 14px;
  border-top-right-radius: 14px;
}

.project-container {
  margin-top: 2em;
  padding: 1em 1em 0 3em;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 35px;
}

.project-card {
  margin-bottom: 1em;
  margin-top: 1em;
  width: 350px;
  height: 530px;
  background-color: #fefffe;
  border-radius: 10px;
  border: 1px solid #000;
  color: #000;
}

.project-card:nth-child(2) {
  margin-top: 1.1em;
  height: 550px;
  transform: scale(0.95);
}

.project-card:hover {
  transform: scale(1.06);
  transition: all 0.6s ease-out;
  cursor: pointer;
}

.img-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.project-description {
  text-align: center;
  margin-top: 1.2em;
  font-size: 17px;
  word-wrap: wrap;
}

.icon-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}


.project__title {
  border-bottom: 2px solid #f00946;
}

.project__card-title {
  margin: 0 auto;
  text-align: center;
}

.icon-social {
  margin: 10px;
  fill: #000;
  width: 35px;
  height: 35px;
}

.icon-social:hover {
  fill: #2ab7ca;
}


@media(max-width: 1124px){
  .project-container { 
    grid-template-columns: repeat(1, 1fr);
  }
}

@media (max-width: 991px) {
  .project-container {
    grid-template-columns: repeat(1, 1fr);
  }
  .project-card {
    /* margin-bottom: 1em;
    margin-bottom: 1em;
    margin-top: 1em; */
    width: 450px;
    height: 530px;
    background-color: #fefffe;
    border-radius: 10px;
    border: 1px solid #000;
    color: #000;
  }
  .img-container img {
    width: 85%;
    height: 100%;
    object-fit: contain;
  }

  .project-description {
    text-align: center;
    margin-top: 2em;
    font-size: 15px;
    word-wrap: wrap;
  }
}

@media (max-width: 768px) {
  .project-container {
    grid-template-columns: repeat(1, 1fr);
  }
  .project-card {
    margin-bottom: 1em;
    margin-bottom: 1em;
    margin-top: 1em;
    width: 450px;
    height: 430px;
    background-color: #fefffe;
    border-radius: 10px;
    border: 1px solid #000;
    color: #000;
  }
  .img-container img {
    width: 68%;
    height: 100%;
    object-fit: contain;
  }

  .project-description {
    text-align: center;
    margin-top: 2em;
    font-size: 16px;
    word-wrap: wrap;
  }
}

@media (max-width: 640px) {
  .project-container {
    grid-template-columns: repeat(1, 1fr);
  }
  .project-card {
    margin-bottom: 1em;
    margin-right: 1em;
    margin-top: 1em;
    width: 250px;
    height: 430px;
    background-color: #fefffe;
    border-radius: 10px;
    border: 1px solid #000;
    color: #000;
  }
}

.home__hero-section {
