}

.home__hero-img-wrapper {
  max-width: 650px;
}

.home__hero-img {
  margin: 0 0 0 150px;
  padding-right: 0;
  max-width: 45%;
  border-radius: 15px;
  -webkit-filter: grayscale(100%) brightness(80%);
          filter: grayscale(100%) brightness(80%);
}

.home__hero-img:hover {
  -webkit-filter: grayscale(0%) brightness(95%);
          filter: grayscale(0%) brightness(95%);
  transition: 1.3s;
  cursor: pointer;
}

img {
  border: 0;
  max-width: 100%;
  vertical-align: middle;
  display: inline-block;
  animation: fadeIn ease-in 3s;
  -webkit-animation: fadeIn ease 3s;
  -moz-animation: fadeIn ease 3s;
  -o-animation: fadeIn ease 3s;
  -ms-animation: fadeIn ease 3s;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

